const Circle = styled.img`
  position: relative;
  left: 20%;
  width: 110%;

  @media (max-width: 991px) {
    width: 120%;
    left: 5%;
  }

  @media (max-width: 767px) {
    width: 150%;
    left: 0;
  }

  @media (min-width: 575px) and (max-width: 610px) {
    width: 150%;
    left: 0;
  }

  @media (max-width: 575px) {
    display: none;
  }
`

